@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-style: $style;
    font-weight: #{$weight};
    font-family: "#{$font-family}";

    font-display: swap;
    src: url("../fonts/#{$url}.woff2") format("woff2"),
    url("../fonts/#{$url}.woff") format("woff");
  }
}

@include font-face("Gotham", "Gotham-Light", 300, normal);
@include font-face("Gotham", "Gotham-Regular", 400, normal);
@include font-face("Gotham", "Gotham-Medium", 500, normal);
@include font-face("Gotham", "Gotham-Bold", 700, normal);

@include font-face("Roboto", "Roboto-Regular", 400, normal);
@include font-face("Roboto", "Roboto-Medium", 500, normal);
@include font-face("Roboto", "Roboto-Black", 900, normal);

@include font-face("Futura-PT", "Futura-PT-Regular", 400, normal);
@include font-face("Futura-PT", "Futura-PT-Medium", 500, normal);

@include font-face("Raleway", "Raleway-Medium", 500, normal);



